<template>
  <v-app>
    <v-app-bar
        fixed
        color="white"
        elevation="1"
    >

      <!--Back button-->
      <v-app-bar-nav-icon>
        <i class="fa fa-arrow-left fa-2x" style="color:#000000;" aria-hidden="true"></i>
      </v-app-bar-nav-icon>

      <v-spacer></v-spacer>

      <!--Edit button-->
      <div class="user_edit_icon">
        <v-btn icon>
          <icon name="user_edit" />
        </v-btn>
      </div>
    </v-app-bar>
    
    <!--Page-->
    <v-main id="main">
      <v-container>
        <div class="content">
          <!--User Info-->
          <div class="w-full p-10 user_container">
              <div class="avatar_container">
                <v-avatar
                    class="elevation-8"
                    size="112"
                >
                  <img :src="userData.avatar" alt="">
                </v-avatar>
              </div>
              <div class="user_info">
              <p class="user_info_header">{{ userData.userName }}</p>
              <p class="user_info_subheader">@{{ userData.userName }}</p>
              </div>
          </div>

          <!--Navigation menu at the top-->
          <v-toolbar class="topMenu">
            <v-btn color="white" :ripple="false">
              My pictures
            </v-btn>
            <v-btn color=white :ripple="false">
              Tags
            </v-btn>
            <v-btn color="white" :ripple="false">
              Badges
            </v-btn>
          </v-toolbar>

          <!--Grid of Photos-->
          <div class="photo_grid" :style="{'grid-template-columns': itemsPerRow}">
            <div class="photo_container" v-for="j in discoveries.length" :key="j">
              <img class="photo" :src="discoveries[j-1].photoPath" alt="">
            </div>
            <div class="photo_container">
              <img class="photo" src="https://www.differencebetween.com/wp-content/uploads/2019/11/Difference-Between-Stem-Tendril-and-Leaf-Tendril_2.jpg" alt="">
            </div>
            <div class="photo_container">
              <img class="photo" src="https://upload.wikimedia.org/wikipedia/commons/5/5e/Sassafras_Leaves_June_Nbg_%28261691941%29.jpeg" alt="">
            </div> 
            <div class="photo_container">
              <img class="photo" src="https://upload.wikimedia.org/wikipedia/commons/b/b7/Bloodroot_%28Sanguinaria_canadensis%29.jpeg" alt="">
            </div> 
          </div>}

          </div>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import Icon from '../../components/Icon';
import axios from 'axios'
export default {
  name: 'App',

  components: {
    Icon
  },

  data: () => ({
    discoveries: null,
    userData: null
  }),

  mounted() {
    axios.get('/public/profile/getOwnDiscoveries').then(response => (this.discoveries = response["data"]))
    axios.get('/public/profile/getUserData').then(response => (this.userData = response["data"]))
  },

  computed: {
    itemsPerRow() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return "repeat(2, 0.1fr)"
        case 'sm':
          return "repeat(2, 0.1fr)"
        case 'md':
          return "repeat(3, 0.1fr)"
        case 'lg':
          return "repeat(4, 0.1fr)"
        case 'xl':
          return "repeat(4, 0.1fr)"
      }
      return 1;
    },
  },
};
</script>

<style>
@import '../styles.css';
#main {
  background-image: url(leaves.png);
  background-color: rgba(255, 255, 255, 0.4);
  background-blend-mode: lighten;
  background-repeat: repeat;
  background-position: center;
}

.topMenu{
  display: grid;
  grid-template-columns: auto;
  margin: 20px;
  padding: 0px;
  place-items: center center;
  justify-content: center;
}

.photo_grid {
  display: grid;
  grid-template-columns: auto;
  margin: 20px;
  padding: 0px;
  place-items: center center;
  justify-content: center;
}

.photo_container {
  display: inline;
  margin: 0px;
  padding: 0px;
  line-height: 5px;
}

.photo{
  width: 20rem;
  height: 20rem;
  vertical-align: middle;
  object-fit:cover;
  border: 1px solid black;
}

.user_container {
  margin-top: 3.5rem;
}

.user_info {
  display: inline-block;
  line-height: 6pt;
  top: 80%;
  font-family: 'Lato', sans-serif;
}

.user_info_header{
  font-size: 24px;
  font-weight: 700;
}

.user_info_subheader{
  font-size: 12px;
  font-weight: 300;
}

.avatar_container {
  display: inline-block;
  margin: 2rem;
}
</style>